header {
  position: fixed;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  background-color: #991f00;
  padding-top: 10px;
}

#header-bottom {
  position: fixed;
  top: 60px;
  height: 10px;
  width: 100%;
  z-index: 100;
  background-color: #626262;
}

.link {
  display: flex;
  align-items: center;
  text-decoration: none;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
  height: 100%;
  color: white;
}

.weapon-link {
  background-color: #330a00;
}

.utility-link {
  background-color: #4d0f00;
}

.core-link {
  background-color: #661400;
}

.wrapper {
  width: 2500px;
  margin-top: 60px;
  background-color: #626262;
}

.row {
  margin-left: 30px;
}

.active {
  border-bottom: none;
  background-color: #626262;
}

.card {
  position: relative;
  display: inline-block;
  width: 280px;
  height: 85px;
  margin: 10px
}

.icon {
  position: absolute;
  top: 18px;
  left: 2px;
  width: 58px;
  height: 58px
}

.text {
  position: absolute;
  top: 2px;
  left: 60px;
  font-family: 'Arimo';
  font-size: 14px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0
}

.component {
  display: flex;
  position: absolute;
  top: 25px;
  left: 65px;
  width: 25px;
  height: 22px;
}

.medium {
  left: 90px;
}

.large {
  left: 115px
}

.popup-wrap {
  position: absolute;
  left: 20px;
  min-width: 170px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.9);
  border: 2px solid #00b300;
  z-index: 10;
}

.top {
  top: 20px
}

.bottom {
  bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  display: flex;
  margin: 4px 0;
  font-size: 14px;
}

.value {
  color: yellow;
}

.key {
  white-space: nowrap;
  color: white;
  margin-right: 8px;
}

.resource-wrap {
  display: inline-flex;
  align-items: center;
}

.resource-type {
  display: inline-block;
  width: 24px;
  height: 24px;
}

.weapon-modifiers {
  color: #00b300;
}